import { Component } from '@angular/core';
import { HttprequestService } from './httprequest.service';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { AirdropAddPopUpComponent } from './airdrop-add-pop-up/airdrop-add-pop-up.component';
import * as Firebase from 'firebase';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent {

  success: String;
  tokens = [];
  airdrops = {};
  error;
  database;

  config = {
    apiKey: 'AIzaSyDfeuS7jrsMShlFu05Ezk8crWicPEy6cIE',
    authDomain: 'https://airfolio-app.firebaseio.com/',
    databaseURL: 'https://airfolio-app.firebaseio.com/',
  };

  constructor(private http: HttprequestService, public dialog: MatDialog) {
    Firebase.initializeApp(this.config);
    this.database = Firebase.database();
    const airdropsRef = this.database.ref('airdrops/');
    airdropsRef.on('value', result => {
      this.airdrops = result.val();
      this.tokens = [];
      for (const key in this.airdrops) {
        if (this.airdrops.hasOwnProperty(key)) {
          const element = this.airdrops[key];
          this.tokens.push(element);
        }
      }
    });
  }

  addAirdrop( ) {
    const dialogRef = this.dialog.open(AirdropAddPopUpComponent, {
      width: '95%',
      height: '80%'
    });

    dialogRef.afterClosed().subscribe(result => {
      const update = {};
      const newPostkey = this.randomString(5, '#aA');
      update[newPostkey] = JSON.parse(JSON.stringify(result));
      update[newPostkey]['id'] = newPostkey;
      update['addedOn'] = new Date().toDateString();
      this.database.ref('airdrops/').update(update);

          this.displaySuccess('Successfully Added');

    });
  }
  randomString(length, chars) {
    let mask = '';
    if (chars.indexOf('a') > -1) { mask += 'abcdefghijklmnopqrstuvwxyz'; }
    if (chars.indexOf('A') > -1) { mask += 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'; }
    if (chars.indexOf('#') > -1) { mask += '0123456789'; }
    if (chars.indexOf('!') > -1) { mask += '~`!@#$%^&*()_+-={}[]:";\'<>?,./|\\'; }
    let result = '';
    for (let i = length; i > 0; --i) { result += mask[Math.floor(Math.random() * mask.length)]; }
    return result;
}

displaySuccess(message: String ) {
  const that = this;
  this.success = message;
  setTimeout(() => {
    that.success = null;
  }, 5000);
}

  displayError(message: String ) {
    const that = this;
    this.error = message;
    setTimeout(() => {
      that.error = null;
    }, 5000);
  }
}



