import { Component, OnInit, Inject } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import { FormControl, FormGroup,  } from '@angular/forms';

@Component({
  selector: 'app-airdrop-add-pop-up',
  templateUrl: './airdrop-add-pop-up.component.html',
  styleUrls: ['./airdrop-add-pop-up.component.css']
})
export class AirdropAddPopUpComponent implements OnInit {

  error;
  step = 0;
  success;

  typeForm = new FormGroup({
    airdrop : new FormControl(),
    reffer : new FormControl(),
    contest : new FormControl(),
    bounty : new FormControl()
  });

  detailsForm = new FormGroup ({
    name : new FormControl(),
    symbol : new FormControl(),
    difficulty : new FormControl(),
    ref_link: new FormControl(),
    icon_url : new FormControl(),
    website : new FormControl(),
    value : new FormControl(),
    endDate : new FormControl(),
  });

  requirementsForm = new FormGroup({
    kyc: new FormControl(),
    eth_wallet: new FormControl(),
    telegram_group: new FormControl(),
    telegram_channel: new FormControl(),
    facebook_like: new FormControl(),
    facebook_comment: new FormControl(),
    facebook_share: new FormControl(),
    facebook_post: new FormControl(),
    twitter_follow: new FormControl(),
    twitter_tweet: new FormControl(),
    twitter_retweet: new FormControl(),
    reddit: new FormControl(),
    medium: new FormControl(),
    youtube: new FormControl()
  });

  formArray = [this.detailsForm, this.typeForm, this.requirementsForm];

  constructor(
    public dialogRef: MatDialogRef<AirdropAddPopUpComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any) {
     }

  ngOnInit() {
  }

  setStep(index: number) {
    this.step = index;
  }

  nextStep() {
    if (this.formArray[this.step].valid) {
      this.step++;
    } else {
      this.displayError('All fields must be properly filled');
    }

  }
  prevStep() {
    this.step--;
  }
  displayError(message: String ) {
    const that = this;
    this.error = message;
    setTimeout(() => {
      that.error = null;
    }, 5000);
  }




  submit() {
    const airdrop_object = JSON.parse(JSON.stringify(this.detailsForm.value));
    airdrop_object['type'] = this.typeForm.value;
    airdrop_object['requirements'] = this.requirementsForm.value;
    this.dialogRef.close(airdrop_object);
  }

}



